
function Home() {
  return (
    <form className="Calc" onSubmit={this.handleSubmit}>
        <div className="Square">
            <label>
                Площадь m<sup>2</sup>:
                <input type='number' name='inputS' />
            </label>
        </div>
        <div className="floors">
            <label>
                Этажи:
                <select name="inputF">
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                </select>
            </label>
        </div>
    </form>
  );
}

export default Home;
